import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {initServiceUrl} from "./Constants";
import Keycloak from "keycloak-js";
import {ReactKeycloakProvider} from '@react-keycloak/web'


const ATTRIBUTES = {
  serviceUrl: 'service-url',
};

const getKeycloakInstance = () =>
  (window &&
    window.entando &&
    window.entando.keycloak && {...window.entando.keycloak, initialized: true}) || {
    initialized: false,
  };

const keycloakConfig = {
  realm: 'entando',
  clientId: 'entando-web',
  url: 'http://34.78.126.51.nip.io/auth'
}
const keycloak = new Keycloak(keycloakConfig)
window.keycloak = keycloak


class WidgetElement extends HTMLElement {

  keycloak = getKeycloakInstance();

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }

  connectedCallback() {
    const serviceUrl = this.getAttribute("service-url")
    initServiceUrl(serviceUrl);

    this.mountPoint = document.createElement('div');
    this.appendChild(this.mountPoint);
    this.keycloak = {...getKeycloakInstance(), initialized: true};

    ReactDOM.render(
      <ReactKeycloakProvider authClient={keycloak} initOptions={{onLoad: 'login-required'}}>
        <App/>
       </ReactKeycloakProvider>
  , this.mountPoint);
  }
}


customElements.define('list-widget', WidgetElement);

export default WidgetElement;

