import React, {useEffect, useState} from 'react';
import {apiListaPratiche} from "../http/api";
import {K} from "../Constants";
import {Col, Row, Table} from "react-bootstrap";
import {getIsProspect} from "../http/helpers";


export const ListaPratica = () => {
  const [pratiche, setPratiche] = useState([]);
  const [pratichePages, setPratichePages] = useState({first: true, last: false, total: 1, current: 0})


  const getListaPratiche = (page) => {
    apiListaPratiche(K.serviceUrl, page , 5)
      .then(json => {
        console.log(json)
        const pagination = {first: json.first, last: json.last, total: json.totalPages, current: json.number}
        setPratichePages(pagination)
        setPratiche(json.content)
      })
      .catch(e => console.log("err get lista"));
  };

  const pagesButtons = (total, onClick) => {
    let buttons = [];
    let firstPageShown = Math.max(0, pratichePages.current - 2);
    let lastPageShown = Math.min(total,  pratichePages.current + 3);
    if (firstPageShown !== 0) {
      buttons.push(<button key={0} onClick={(event) => onClick(event.target.innerText)}>1</button>);
      if (firstPageShown !== 1) {
        buttons.push(<button key={'prec-pages'} className={'pagination-button-disabled'}>...</button>);
      }
    }
    for (let i = firstPageShown; i < lastPageShown; i++) {
      buttons.push(<button key={i} onClick={(event) => onClick(event.target.innerText)} className={pratichePages.current === i ? 'strong' : ''}>{i+1}</button>);
    }
    if (lastPageShown !== total) {
      if (lastPageShown !== total-1) {
        buttons.push(<button key={'foll-pages'} className={'pagination-button-disabled'}>...</button>);
      }
      buttons.push(<button key={total-1} onClick={(event) => onClick(event.target.innerText)}>{total}</button>);
    }
    return buttons;
  };
  const changePage = (targetPage) => {
    getListaPratiche(Number.parseInt(targetPage)-1);
  };


  useEffect(() => {
    if(!getIsProspect())
    getListaPratiche(0);
  }, []);

  return (
    <>
      <div className={'back-col'}>
        <p className={'title m-4'}>Fast Lending</p>
        <p className={'subtitle m-4'}>Lista pratiche Fast Lending</p>
        <hr className={'m-4'}/>
        <div className={'white-rounded-shadowed m-4'}>
          <Row className={"align-content-center"}>
            <Table responsive>
              <thead className={'table-head'}>
              <tr>
                <th>ID</th>
                <th>CLIENTE</th>
                <th>DATA APERTURA</th>
                <th>PRODOTTO</th>
                <th>STATO PRATICA</th>
              </tr>
              </thead>
              <tbody className={'table-data '}>
              {pratiche.map((pratica, i) =>
                <tr key={i} className={'table-row'}>
                  <td className={'primary-color id-pointer text-decoration-underline'}
                      onClick={event =>  window.location.href = "/entando-de-app/en/dettaglio.page?guid=" + pratica.guid}>{pratica.id}</td>
                  <td className={"fw-bolder"}>{pratica.cliente}</td>
                  <td className={"data"}>{pratica.dataApertura}</td>
                  <td>{pratica.prodotto}</td>
                  <td><span className={'stato'}>{pratica.statoPratica}</span></td>
                </tr>
              )}
              </tbody>
            </Table>
          </Row>
        </div>
        <Row>
          <Col md={8}></Col>
          <Col md={4} className={'mt-2 pagination-button align-to-end '} >
            <button disabled={pratichePages.first} className={pratichePages.first ? 'button-disabled' : ''} onClick={() => getListaPratiche(pratichePages.current-1)}>Indietro</button>
            {pagesButtons(pratichePages.total, changePage)}
            <button disabled={pratichePages.last} className={pratichePages.last ? 'button-disabled' : ''} onClick={() => getListaPratiche(pratichePages.current+1)}>Avanti</button>
          </Col>
        </Row>

      </div>
    </>);
}


