import {getDefaultOptions, request} from "./helpers";


const get = (url) => {
  const options = {
    ...getDefaultOptions(),
    method: 'GET'
  };
  return request(url, options);
};


export const apiListaPratiche = async (serviceUrl, page, size) => {
  return get(`${serviceUrl}/api/pratica/all?page=${page}&size=${size}`);
};
